.title-h1 {
	@include gilroy-medium;
	font-size: 30px;
	color: #000;
	letter-spacing: -0.4px;
	@include rmin(768) {
		font-size: 42px;
		letter-spacing: -0.56px; } }

.title-h2 {
	margin-bottom: 40px;
	@include gilroy-bold;
	font-size: 30px;
	letter-spacing: -0.46px; }

.title-h3 {
	margin-bottom: 11px;
	font-size: 24px;
	@include gilroy-medium;
	line-height: 1.375;
	letter-spacing: -0.56px; }

.title-h4 {
	@include gilroy-bold;
	font-size: 16px;
	letter-spacing: -0.25px; }

.title-h5 {
	font-size: 14px;
	@include gilroy-bold;
	line-height: 1.35;
	letter-spacing: 0.97px; }

.special-title {
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 650px;
	min-height: 109px;
	margin: 0 auto;
	padding: 0 10px;
	text-align: center;
	@include gilroy-medium;
	font-size: 22px;
	line-height: 1.363;
	letter-spacing: 0.78px;
	&--more-spacing {
		margin: 23px auto 27px; }
	@include rmin(768) {
		max-width: 550px;
		@include gilroy-bold;
		font-size: 27px;
		letter-spacing: 1.14px;
		line-height: 1.375;
		br {
			display: none; } }
	@include rmin(1025) {
		max-width: 745px;
		margin: 28px auto 22px;
		font-size: 32px; } }
