@include font(caslon, caslon);
@include font(caslon-semibold, caslon-semibold);
@include font(gilroy-regular, gilroy-regular);
@include font(gilroy-medium, gilroy-medium);
@include font(gilroy-bold, gilroy-bold);


@mixin caslon {
	font-family: 'caslon', serif; }

@mixin caslon-semibold {
	font-family: 'caslon-semibold', serif; }

@mixin gilroy {
	font-family: 'gilroy-regular', sans-serif; }

@mixin gilroy-medium {
	font-family: 'gilroy-medium', sans-serif; }

@mixin gilroy-bold {
	font-family: 'gilroy-bold', sans-serif; }

