.form {
	&__row {
		display: flex;
		margin: 0 -5px 10px;
		&--md-margin-b {
			margin-bottom: 20px; }
		&--lg-margin-b {
			margin-bottom: 57px; }
		&--no-negative-margin {
			margin-left: 0;
			margin-right: 0; } }
	&__field {
		position: relative;
		flex-basis: 100%;
		margin: 0 5px;
		&.is-active {
			.form__label {
				top: 9px;
				font-size: 9px; }
			.form__input {
				padding-top: 12px;
				padding-bottom: 13px; } }
		&.is-error {
			.form__input {
				padding-top: 23px;
				padding-bottom: 24px; }
			.form__input,
			.form__select {
				border-color: #E34F30; }
			.form__helper {
				display: block; }
			.form__label {
				top: 12px;
				font-size: 12px; } } }
	&__label {
		position: absolute;
		top: 50%;
		left: 9px;
		margin: -6px 0 0;
		text-transform: uppercase;
		@include gilroy-medium;
		font-size: 12px;
		color: #008489;
		line-height: 1;
		letter-spacing: -0.16px;
		transition: top 0.1s linear;
		small {
			font-size: 12px;
			text-transform: none; } }
	&__input {
		display: block;
		width: 100%;
		padding: 14px 9px 15px;
		@include gilroy-medium;
		font-size: 12px;
		border: 1px solid #ddd;
		line-height: 1;
		letter-spacing: -0.16px;
		transition: border-color 0.2s ease;
		border-radius: 3px; }
	&__helper {
		display: none;
		position: absolute;
		left: 9px;
		bottom: 4px;
		@include gilroy-medium;
		font-size: 12px;
		color: #E34F30; }
	&__select {
		width: 100%;
		height: 46px;
		padding: 10px 9px;
		@include gilroy-medium;
		font-size: 12px;
		-webkit-appearance: none;
		background-color: #fff;
		background-image: url(data:image/png;base64,);
		background-position: right center;
		background-repeat: no-repeat;
		border: 1px solid #ddd;
		border-radius: 3px; }
	&__link {
		@include gilroy-bold;
		font-size: 12px;
		letter-spacing: 1.13px;
		&--txt-normal {
			@include gilroy-medium;
			font-size: 14px;
			letter-spacing: 1.3; }
		&--line-height {
			line-height: 1; } }
	&__suggest {
		font-size: 12px;
		letter-spacing: 1.13px;
		line-height: 1; }
	&__button {
		width: 100%;
		padding: 15px 10px 14px;
		text-transform: uppercase;
		@include gilroy;
		font-size: 18px;
		background: #008489;
		color: #fff;
		border-radius: 4px;
		letter-spacing: -0.2px;
		line-height: 1.2;
		&:focus {
			outline: #008489; } }
	&__txt-bold {
		@include gilroy-bold; } }

.field {
	position: relative;
	display: block;
	text-decoration: none;
	margin: 0 -20px;
	padding: 18px 57px 18px 22px;
	@include gilroy-medium;
	font-size: 12px;
	line-height: 1.33;
	color: #008489;
	border-top: 2px solid #f2f2f2;
	letter-spacing: -0.16px;
	&:hover {
		text-decoration: none;
		color: #008489; }
	&__title {
		text-transform: uppercase;
		@include gilroy-bold;
		color: #000; }
	&__info {
		margin-top: 5px;
		&--flex {
			display: flex;
			max-width: 100%;
			justify-content: space-between; } }
	&__el {
		max-width: 120px; }
	&__arrow {
		position: absolute;
		top: 50%;
		right: 21px;
		margin-top: -10px;
		font-size: 20px;
		svg {
			fill: #008489; } }
	&__icon {
		color: #000;
		font-size: 16px;
		vertical-align: middle;
		line-height: 1; }
	&__cancel {
		margin-left: 14px; } }
